.selected-parts{
  display: flex;
}

.select-panel{
  position: absolute;
  min-width: 150px;
}
.select-item{
  width: 24px;
  height: 24px;
}
.select-area{
  position: absolute;
  min-width: 150px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(24px, 1fr));
  background-color: gainsboro;
  border: solid;
  border-color: gray;
  max-height: calc(40vh - 100px);
  overflow-y: auto;
}
.selected-part{
  display: flex;
  flex-direction: column;
}
.icon-part{
  display: flex;
}
